"use client"

import React, { useEffect, useRef, useState } from "react"
import { AlertError } from "@/components/alertError"
import { Button } from "@/components/shadcn/ui/button"
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/shadcn/ui/card"
import { Input } from "@/components/shadcn/ui/input"
import { FilterIcon, Star, StarOff, Globe, Lock } from "lucide-react"
import { Badge } from "@/components/shadcn/ui/badge"

import {
  Dialog,
  DialogClose,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/shadcn/ui/dialog"
import { Label } from "@/components/shadcn/ui/label"

import { useSession } from "next-auth/react"
import { useRouter } from "next/navigation"

type ItemProps = {
  _id: string
  title: string
  isPublic: boolean
  isStarred: boolean
  language: string
  owner: string
  updatedAt: string
}

const LANGUAGES = [
  { code: 'en', name: 'English' },
  { code: 'vi', name: 'Tiếng Việt' },
]

function LibraryCard({ item }: { item: ItemProps }) {
  const router = useRouter()
  return (
    <div
      onClick={() => router.push(`/education/languages/vocabulary/library/${item._id}/${item.title}`)}
      className="rounded-xl bg-gray-100 dark:bg-gray-800 p-4 shadow hover:shadow-md hover:bg-gray-200 dark:hover:bg-gray-700 transition-all cursor-pointer"
    >
      <div className="flex justify-between items-start mb-3">
        <div className="flex items-center space-x-2">
          <h3 className="text-base font-semibold">{item.title}</h3>
          <Badge variant="outline" className="ml-auto text-xs p-1">{item.isPublic ? "Công khai" : "Riêng tư"}</Badge>
        </div>
        {item.isStarred ? (
          <Star className="w-4 h-4 text-yellow-400" />
        ) : (
          <StarOff className="w-4 h-4 text-gray-400" />
        )}
      </div>


      <div className="flex items-center space-x-2">
        # {item.language}
        <Badge variant="outline" className="ml-auto text-xs">
          <div className="col-span-2">
            <span className="font-medium">Cập nhật:</span>{" "}
            {new Date(item.updatedAt).toLocaleDateString("vi-VN")}
          </div>
        </Badge>
      </div>
    </div>
  )
}


export function MyCard() {
  const [visibleItems, setVisibleItems] = useState<ItemProps[]>([])
  const itemsPerLoad = 12
  const loadMoreRef = useRef<HTMLDivElement | null>(null)
  const loadedCountRef = useRef<number>(0)

  useEffect(() => {
    // Load initial items
    const initialItems = dataList.slice(0, itemsPerLoad)
    setVisibleItems(initialItems)
    loadedCountRef.current = itemsPerLoad
  }, [])

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        const isVisible = entries[0].isIntersecting
        if (isVisible) {
          const next = dataList.slice(
            loadedCountRef.current,
            loadedCountRef.current + itemsPerLoad
          )
          if (next.length > 0) {
            setVisibleItems((prev) => [...prev, ...next])
            loadedCountRef.current += itemsPerLoad
          }
        }
      },
      {
        rootMargin: "0px",
        threshold: 1.0,
      }
    )

    if (loadMoreRef.current) {
      observer.observe(loadMoreRef.current)
    }

    return () => {
      if (loadMoreRef.current) {
        observer.unobserve(loadMoreRef.current)
      }
    }
  }, [])

  const [error, setError] = useState<string | null>(null)
  const { data: session } = useSession()
  const [libraryForm, setlibraryForm] = useState({ title: "", language: "" })

  const handleChangeLibraryForm = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setlibraryForm({ ...libraryForm, [e.target.name]: e.target.value })
  }
  const handleAddLibrary = async (e: React.FormEvent) => {
    e.preventDefault()
    const user = session?.user
    const { title, language } = libraryForm

    try {
      const res = await fetch("/api/dictionary/library", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ title, language, user: user }),
      })
      const data = await res.json()
      if (res.ok) {
        setlibraryForm({ title: "", language: "" })
        alert("Thêm thư viện thành công")
      } 
      else {
        setError(data.message || "Lỗi khi thêm thư viện")
      }
    }
    catch (error) {
      console.error("Error adding library:", error)
      setError("Lỗi khi thêm thư viện. Vui lòng thử lại sau.")
    }
  }


  const [dataList, setDataList] = useState<ItemProps[]>([]);

  useEffect(() => {
    const user = session?.user
    if (!user) {
      setError("Bạn cần đăng nhập để xem thư viện")
      return
    }
    const fetchData = async () => {
      try {
        const res = await fetch(`/api/dictionary/library`, {
          method: "GET",
          headers: {
            'Content-Type': 'application/json',
            'userId': user.id ?? '', 
          },
        });

        if (!res.ok) {
          throw new Error("Failed to fetch libraries");
        }

        const data = await res.json();
        setDataList(data);
        const initialItems = data.slice(0, itemsPerLoad)
      setVisibleItems(initialItems)
      loadedCountRef.current = initialItems.length
      } catch (error) {
        console.error("Error fetching libraries:", error);
      }
}

    fetchData()
  }, [session?.user])

  return (
    <>
      <Card>
        <CardHeader>
          <CardTitle>Thư viện của tôi</CardTitle>
          <div className="flex items-center justify-between mt-2">
            <div className="flex items-center space-x-2">
              <Input placeholder="Tìm kiếm..." className="w-[200px]" />
              <Button variant="outline" size="icon">
                <FilterIcon className="h-4 w-4" />
              </Button>
            </div>
            <Dialog>
              <form>
                <DialogTrigger asChild>
                  <Button variant="outline" >Add library</Button>
                </DialogTrigger>
                <DialogContent className="sm:max-w-[425px]">
                  <DialogHeader>
                    <DialogTitle>Thêm thư viện</DialogTitle>
                    <DialogDescription>
                      Nhập thông tin thư viện. Nhấn "Lưu" để tạo mới.
                    </DialogDescription>
                  </DialogHeader>
                  <form onSubmit={handleAddLibrary}>
                    {error && (
                      <AlertError>
                        {error}
                      </AlertError>
                    )}
                    <div className="grid gap-4 py-4">
                      <div className="grid gap-3">
                        <Label htmlFor="title">Tên thư viện</Label>
                        <Input name="title" value={libraryForm.title} onChange={handleChangeLibraryForm} required />
                      </div>
                      <div className="grid gap-3">
                        <Label htmlFor="language">Ngôn ngữ</Label>
                        <select
                          name="language"
                          value={libraryForm.language}
                          onChange={handleChangeLibraryForm}
                          className="w-full px-3 py-2 border rounded-md text-sm"
                        >
                          <option value="" disabled>Chọn ngôn ngữ</option>
                          {LANGUAGES.map((lang) => (
                            <option key={lang.code} value={lang.code}>
                              {lang.name}
                            </option>
                          ))}
                        </select>
                      </div>


                    </div>
                    <DialogFooter>
                      <DialogClose asChild>
                        <Button type="button" variant="outline">Hủy</Button>
                      </DialogClose>
                      <Button type="submit">Lưu</Button>
                    </DialogFooter>
                  </form>
                </DialogContent>

              </form>
            </Dialog>
          </div>
        </CardHeader>

        <CardContent>
          <div className="grid grid-cols-1 gap-4">
            {visibleItems.map((item) => (
              <LibraryCard key={item._id} item={item} />
            ))}
          </div>

          <div
            ref={loadMoreRef}
            className="h-10 mt-4 flex justify-center items-center"
          >
            {visibleItems.length < dataList.length ? (
              <span className="text-sm text-muted-foreground">
                Đang tải thêm...
              </span>
            ) : (
              <span className="text-sm text-muted-foreground">
                Đã tải hết nội dung
              </span>
            )}
          </div>
        </CardContent>
      </Card>
    </>
  )
}
